.about-middle {
	background-image: url(../img/about/davinci.png);
	margin-left: 7.5vw;
	margin-right: 4.5vw;
	margin-bottom: 5vw;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: right;
	h2 {
		font-size: 2.8vw;
		padding-top: 6vw;
		line-height: 3.4vw;
		letter-spacing: .24em;
		margin-bottom: 2.5vw;
		font-family: ten-mincho-text, serif;
		font-weight: 400;
		text-align: initial;
		font-style: normal;
	}
	h3 {
		font-size: 2.3vw;
		line-height: 3.4vw;
		letter-spacing: .24em;
		font-family: ten-mincho-text, serif;
		margin-bottom: 2.4vw;
	}
	p {
		letter-spacing: .24em;
		font-size: 1vw;
		line-height: 2.4vw;
	}
}